import {createSlice} from '@reduxjs/toolkit';

//Will be a list of objects
//{'url', 'rect', 'isSelected', 'index'}

const pagesSlice = createSlice({
  name: 'pages',
  initialState: [],
  reducers: {
    setPagesFromURLs(state, action){
      //if (state.length > 0){
      //  return(state);
      //}
      return(action.payload.map((url, i) => {
        return({ url: url, rect: {}, isSelected: true, index: i, color: {} });
      })
      )
    },

    setDefaultRect(state, action){
      state.forEach((page) => page.rect = action.payload);
    },
    setRect(state, action){
      const {id, new_rect} = action.payload;
      const page = state.find(page => page.index === id);
      page.rect = new_rect;
    },
    setPageColor(state, action){
      const {id, color} = action. payload;
      const page = state.find(page => page.index === id);
      page.color = color;
    },
    toggleSelected(state, action){
      const page = state.find(page => page.index === action.payload);
      page.isSelected = !page.isSelected;
    }
  }
});

  //{image_urls, colors} = useSelector(getSelectedPlotInfo);

export const getSelectedPlotInfo = state => {
  return(state.pages.filter(page => page.isSelected));
}


export const { setPagesFromURLs, setDefaultRect, setRect, toggleSelected, setPageColor} = pagesSlice.actions

export default pagesSlice.reducer
