import {createSlice} from '@reduxjs/toolkit';

//Will be a list of objects
//{'url', 'rect', 'isSelected', 'index'}

const bookSlice = createSlice({
  name: 'book',
  initialState: {
    lib: '',
    url1: '',
    url2: '',
    manifest_url: '',
    usePercent: true,
    roundPercent: true,
  },
  reducers: {
    setLib(state, action){
      state.lib = action.payload;
    },
    setURL(state, action){
      state.url1 = action.payload.url1;
      action.payload.url2 && (state.url2 = action.payload.url2);
    },
    setManifestURL(state, action){
      state.manifest_url = action.payload;
    },
    setIIIF(state,action){
      state.usePercent = action.payload.usePercent;
      state.roundPercent = action.payload.roundPercent;
    }
  }
});

export const getLibAndUrls = (state) => {
  return([state.book.lib, state.book.url1, state.book.url2]);
}

export const {setLib, setURL, setManifestURL, setIIIF} = bookSlice.actions

export default bookSlice.reducer
