import { React, useState, useEffect, useRef } from "react";

import styled, { css } from "styled-components";

import Chart from "react-google-charts";

import { useLocation } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

import { getSelectedPlotInfo } from "../redux/pagesSlice.jsx";

const PeekImg = styled.img`
  ${({ hiding }) =>
    hiding &&
    `
  visibility: hidden;
  `}
`;

const InstructionDiv = styled.div`
  background: #eeeeee;
  border-style: solid;
  border-width: 2px;
  border-color: #cccccc;
  max-width: 800px;
  text-align: center;
  margin: 20px auto;
  padding: 20px;
`;
const RadioDiv = styled.div`
  background: #eeeeee;
  border-style: solid;
  border-width: 2px;
  border-color: #cccccc;
  text-align: center;
  margin: 10px;
  padding: 10px;
`;
const download_data = (pages) => {
  let csv =
    "index,rect_left,rect_right,rect_bottom,rect_top,med_L,med_U,med_V,med_R,med_G,med_B,mean_L,mean_U,mean_V,mean_R,mean_G,mean_B,URL\n";
  csv =
    csv +
    pages
      .map(
        (x) =>
          [
            x.index,
            x.rect.left,
            x.rect.right,
            x.rect.bottom,
            x.rect.top,
            ...x.color.med_luv,
            ...x.color.med_rgb,
            ...x.color.mean_luv,
            ...x.color.mean_rgb,
            x.url,
          ].join(",") + "\n"
      )
      .reduce((acc, cur) => acc + cur);
  const filename = "export.csv";
  csv = "data:text/csv;charset=utf-8," + csv;
  const csv_enc = encodeURI(csv);
  const link = document.createElement("a");
  link.setAttribute("href", csv_enc);
  link.setAttribute("download", filename);
  link.click();
};

export default function PaperPlot(props) {
  const imageRef = useRef();
  const rectCanvasRef = useRef(); //for drawing rectangle
  const usePercent = useSelector((state) => state.book.usePercent);

  //const location = useLocation();

  const [data_color, setDataColor] = useState("luv");
  const [data_summary, setDataSummary] = useState("med");
  const [plot_width, setPlotwidth] = useState("split");

  const lib = useSelector((state) => state.book.lib);

  const selected_pages = useSelector(getSelectedPlotInfo);

  const handleColorChange = (e) => {
    setDataColor(e.target.value);
  };
  const handleSummaryChange = (e) => {
    setDataSummary(e.target.value);
  };
  const handlePlotwidthChange = (e) => {
    setPlotwidth(e.target.value);
  };

  const selectImage = (i) => {
    const lineWidth = 5;
    const rectColor = "#00ff00";
    const fullurl = selected_pages[i].url;
    const smallurl = fullurl.replace("full/full", "full/500,");
    imageRef.current.src = smallurl;
    const pagerect = selected_pages[i].rect;
    //Set up canvas, draw rectangle.
    const cvs = rectCanvasRef.current;
    const ctx = cvs.getContext("2d");
    const image_height = imageRef.current.naturalHeight; //can drop natural for old one
    const image_width = imageRef.current.naturalWidth;
    cvs.width = image_width;
    cvs.height = image_height;
    if (usePercent) {
      pagerect.top = Math.round((pagerect.top * image_height) / 100);
      pagerect.bottom = Math.round((pagerect.bottom * image_height) / 100);
      pagerect.left = Math.round((pagerect.left * image_width) / 100);
      pagerect.right = Math.round((pagerect.right * image_width) / 100);
    }
    //now draw the box
    console.log("pagerect", pagerect);
    ctx.beginPath();
    ctx.lineWidth = lineWidth;
    ctx.strokeStyle = rectColor;
    ctx.rect(
      pagerect.left,
      pagerect.top,
      pagerect.right - pagerect.left,
      pagerect.bottom - pagerect.top
    );
    ctx.stroke();
    //Left something broken here, or maybe untested. some component error.  Also, does the wait chris thing turn off?
    //FIX
  };

  const clearSelectedImage = () => {
    imageRef.current.src = "";
    //ERASE CANVAS FIX
  };

  // Add switches here for rgb/luv and med/median, once I add that to
  // interface.
  const plotdata = selected_pages.map((page, i) => [
    i,
    ...page.color[data_summary + "_" + data_color],
  ]);
  const var_label = data_color === "luv" ? ["L", "U", "V"] : ["R", "G", "B"];

  return (
    <div style={{ padding: "10px" }}>
      {selected_pages.length > 0 && (
        <div>
          <InstructionDiv>
            Click on a point in the graph to view the corresponding page.
            <br />
            <br />
            Download your data here:{" "}
            <button onClick={() => download_data(selected_pages)}>Data</button>
            <br />
            <div
              style={{
                display: "flex",
                "justify-content": "space-evenly",
                "margin-top": "10px",
              }}
            >
              <RadioDiv>
                <input
                  type="radio"
                  value="luv"
                  id="luv"
                  onChange={handleColorChange}
                  name="colorpicker"
                  checked={data_color == "luv" ? "checked" : ""}
                />
                <label for="luv">LUV</label>
                &nbsp; &nbsp; &nbsp;
                <input
                  type="radio"
                  value="rgb"
                  id="rgb"
                  onChange={handleColorChange}
                  name="colorpicker"
                  checked={data_color != "luv" ? "checked" : ""}
                />
                <label for="rgb">RGB</label>
              </RadioDiv>

              <RadioDiv>
                <input
                  type="radio"
                  value="med"
                  id="med"
                  onChange={handleSummaryChange}
                  name="summarypicker"
                  checked={data_summary == "med" ? "checked" : ""}
                />
                <label for="med">Median</label>
                &nbsp; &nbsp; &nbsp;
                <input
                  type="radio"
                  value="mean"
                  id="mean"
                  onChange={handleSummaryChange}
                  name="summarypicker"
                  checked={data_summary != "med" ? "checked" : ""}
                />
                <label for="mean">Mean</label>
              </RadioDiv>

              {/*  <RadioDiv>
      <input type="radio" value="split" id="split" onChange={handlePlotwidthChange} name="plotwidthpicker"  checked={plot_width=='split'?"checked":""}/>
      <label for="split">Narrow Plot</label>
      &nbsp; &nbsp; &nbsp;
      <input type="radio" value="full" id="full" onChange={handlePlotwidthChange} name="plotwidthpicker" checked={plot_width!='split'?"checked":""} />
      <label for="full">Wide Plot</label>
</RadioDiv>*/}
            </div>
          </InstructionDiv>

          <div
            style={{ display: "flex", margin: "30px", "flex-wrap": "wrap" }}
          >
            <div>
              <Chart
                width={"800px"}
                height={"600px"}
                chartType="Scatter"
                data={[["Page", ...var_label], ...plotdata]}
                options={{
                  legend: { position: "bottom" },
                  title: "Test",
                  colors: ["#FF0000", "#00FF00", "#0000FF"],
                }}
                chartEvents={[
                  {
                    eventName: "select",
                    callback: ({ chartWrapper }) => {
                      const chart = chartWrapper.getChart();
                      const selection = chart.getSelection();
                      if (selection.length === 0) {
                        clearSelectedImage();
                      }
                      if (selection.length === 1) {
                        const [selectedItem] = selection;
                        //const dataTable = chartWrapper.getDataTable()
                        const { row, column } = selectedItem;
                        selectImage(row);
                      }
                    },
                  },
                ]}
              />
            </div>
            <div style={{ position: "relative" }}>
              <PeekImg
                ref={imageRef}
                src=""
                style={{
                  position: "absolute",
                  flex: 1,
                  width: "500px",
                  marginLeft: "30px",
                }}
              />

              <canvas
                width={50}
                height={50}
                ref={rectCanvasRef}
                style={{ position: "absolute", zIndex: 2, marginLeft: "30px" }}
              />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
