import React from 'react';
import {useRef, useState, useEffect, useMemo} from 'react';
import styled, { css } from 'styled-components';

import RectSelect from './RectSelect.jsx';
import ReactModal from 'react-modal';
import {Link, useLocation} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';

import PaperScrap from './PaperScrap.jsx';
import {toggleSelected, setRect, setPageColor} from '../redux/pagesSlice.jsx';
import rgbToLuv from '../rgbToLuv.js';
import {median, mean} from '../summaries.js';

const ImgBoxDiv = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: row;
  background: #eeeeee;
  flex-wrap: wrap;
  padding-bottom: 50px;
  padding-top: 25px;
`
const customStyles = {
  overlay: {zIndex: 1000,
  backgroundColor: "gray"}
};

const InstructionDiv = styled.div`
  background: #eeeeee;
  border-style: solid;
  border-width: 2px;
  border-color: #cccccc;
  max-width: 800px;
  text-align: justify;
  margin: 20px auto;
  padding: 20px;
`

export default function ScrapTable(props) {

  const dispatch = useDispatch();

  const location = useLocation();

  const canvasRef = useRef();

  const [edit_idx_and_modal, setEditIdxAndModal] = useState({modal: false, index: null});
  const [n_loaded, setNLoaded] = useState(0);
  
  const lib = useSelector(state => state.book.lib);
  const img_urls = useSelector(state => state.pages.map(page => page.url));

  //const rectFromSearch = (search) => {
  //  const query = new URLSearchParams(location.search);
  //  const rect = {left: query.get('l'),
  //    right: query.get('r'),
  //    top: query.get('t'),
  //    bottom: query.get('b'),
  //  }
  //  return(rect);
  //}

  //const [base_rect, setBaseRect] = useState(rectFromSearch(location.search));
  //useEffect(() => {
  //  setBaseRect(rectFromSearch(location.search));
  //}, [location.search]);

  //May have to fix this for that library that uses underscores in the IDs.
  //const [url12, setUrl12] = useState(props.match.params.book.split('_'));
  //useEffect(() => {
  //  setUrl12(props.match.params.book.split('_'));
  //}, [props.match.params.book]);
 
  //const [lib, setLib] = useState(props.match.params.lib);
  //useEffect(() => {
  //  setLib(props.match.params.lib);
  //}, [props.match.params.lib]);



  const imageRefs = useMemo(
    () => img_urls.map(()=> React.createRef()),
    [img_urls.length, img_urls]
  );

  const replaceRect = (i, rect) => {
    dispatch(setRect({id: i, new_rect: rect}));
  }

  const getImageData = (imageRef) => {
    const cvs = canvasRef.current
    const ctx = cvs.getContext('2d');
    const image_height = imageRef.current.naturalHeight; //can drop natural for old one
    const image_width = imageRef.current.naturalWidth;
    cvs.width = image_width;
    cvs.height = image_height;
    ctx.drawImage(imageRef.current, 0,0);
    const imgData = ctx.getImageData(0,0,image_width, image_height);
    return imgData.data;//just the flat RGBA array

  }

  const summarizeImage = (data) => {
    let Rarray = []; let Garray = []; let Barray = [];
    let Larray = []; let Uarray = []; let Varray = [];
    for (let i = 0; i < data.length; i += 4) {
      let rgb = data.slice(i, i+3);
      Rarray.push(rgb[0]);     // red
      Garray.push(rgb[1]); // green
      Barray.push(rgb[2]); // blue
      //Now we'll switch to luv
      let luv = rgbToLuv(rgb);
      Larray.push(luv[0]); 
      Uarray.push(luv[1]); 
      Varray.push(luv[2]); 
    }
    return({
      med_rgb: [median(Rarray), median(Garray), median(Barray)],
      med_luv: [median(Larray), median(Uarray), median(Varray)],
      mean_rgb: [mean(Rarray), mean(Garray), mean(Barray)],
      mean_luv: [mean(Larray), mean(Uarray), mean(Varray)]
    });
  }

  //mean version
  //const summarizeImage = (data) => {
  //  let Rsum = 0; let Gsum = 0; let Bsum = 0;
  //  for (let i = 0; i < data.length; i += 4) {
  //    //Now we'll switch to luv
  //    let luv = rgbToLuv(data.slice(i, i+3));
  //    Rsum  += luv[0];     // red
  //    Gsum += luv[1]; // green
  //    Bsum += luv[2]; // blue
  //    //Rsum  += data[i];     // red
  //    //Gsum += data[i + 1]; // green
  //    //Bsum += data[i + 2]; // blue
  //  }
  //  return [Rsum*4/data.length, Gsum*4/data.length, Bsum*4/data.length];
  //}



  const processImage = (i) => {
    const data = getImageData(imageRefs[i]);
    const image_color = summarizeImage(data);
    dispatch(setPageColor({id: i, color: image_color}));
    setNLoaded( n => n + 1 );//increment count of loaded images to disable button.
  }
 
  const handleCloseModal=  () => {
    setEditIdxAndModal({modal: false, index: null});
  }

  const editRect = (i) => {
      setEditIdxAndModal({modal: true, index: i});
    }

  // left top right bottom
  return(
    <div>
    <div style={{textAlign: "center"}}>
    <InstructionDiv>
    Remove any unwanted pages, and correct any poorly-cropped pages.  Try to select sections of paper without ink or stains, while keeping selections in approximately the same region of the page.<br/><br/>

    When you are satisfied, proceed here:<br/><br/>
    {n_loaded >= img_urls.length &&
    <Link to={{
      pathname: `/plot/`,
    }}
    ><button>Extract Color Information</button></Link>}
    {n_loaded < img_urls.length &&
        <p>Please wait until all pages have loaded below.</p>}

    </InstructionDiv>

    <div>
         <ImgBoxDiv>
      {imageRefs.map((ref, i) => {
        return(<PaperScrap key={i} index={i} imageRef={ref}
        process = {() => processImage(i)}
        editRect = {() => editRect(i)}
        />)})}

        </ImgBoxDiv>
 

      </div>

    <canvas ref={canvasRef} width="100px" height="100px" style={{visibility: "hidden"}}/>
    </div>
    <ReactModal isOpen={edit_idx_and_modal.modal}
    style={customStyles}
    onRequestClose={handleCloseModal}
    >
    <RectSelect 
    usePercent={props.usePercent && lib !== 'HRC'}
    roundPercent={lib !== 'HRC'}
    img_urls={[img_urls[edit_idx_and_modal.index]]}
    recordRect={(rect) => replaceRect(edit_idx_and_modal.index, rect)}
    />

    </ReactModal>
    </div>
  );
}
