


import React from 'react';
import {useRef, useState, useEffect, useMemo} from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {useSelector} from 'react-redux';

const ClearDiv = styled.div`
left:0;
&:before,
&:after {
   content: '\\0020';
   display: block;
   overflow: hidden;
   visibility: hidden;
   width: 0;
   height: 0;
}
&:after { clear: both; }
zoom: 1; 
`

export default function RectSelect(props) {

  const canvasRef = useRef();              
  const canvasRef2 = useRef();              
  const imageRef = useRef();
  const [imgWH, setImgWH] = useState({width: '100%', height: '100%'});
  const [maxWH, setMaxWH] = useState({width: null, height: null});
  const [cvs_ready, setCvsReady] = useState(false);
  const usePercent = useSelector(state => state.book.usePercent);
  const roundPercent = useSelector(state => state.book.roundPercent);
    
  const imageRefs = useMemo(
    () => props.img_urls.map(()=> React.createRef()),
    [props.img_urls.join(',')]
  );

  const [numLoaded, setNumLoaded] = useState(0);
  const rectColor = '#FF0000';
  const lineWidth = 5;

  let isDown = false;
  let startX = null;
  let startY = null;
  let endX = null;
  let endY = null;

  const loadListen = () => {
    if (numLoaded + 1 >= props.img_urls.length){
      renderComposite();
    }
    setNumLoaded(num => num+1);
  }

  //COME BACK TO THIS BLOCK
  useEffect( () => {
    const ctx = canvasRef.current.getContext('2d');
    ctx.fillText("Please wait, loading...", 100, 10);
  }, []);
    

  const renderComposite = () => {
    const max_w = Math.max(...imageRefs.map((ref) => ref.current.width));
    const max_h = Math.max(...imageRefs.map((ref) => ref.current.height));
    const cvs = canvasRef.current;
    const cvs2 = canvasRef2.current;
    const ctx = cvs.getContext('2d');
    const ctx2 = cvs2.getContext('2d');
    cvs.width = max_w;
    cvs.height = max_h;
    cvs2.width = max_w;
    cvs2.height = max_h;
    ctx.fillStyle = 'white';
    ctx.fillRect(0,0,max_w,max_h);
    let newData = ctx.getImageData(0,0,max_w,max_h);
    //this.ctx.createImageData(max_w, max_h);
    for (let i=0; i<imageRefs.length; i++){
      ctx.drawImage(imageRefs[i].current, 0,0);
      let imgdat = ctx.getImageData(0,0,max_w, max_h);

      //looping explicitly because we'll do LUV later, maybe
      for (let j = 0; j < newData.data.length; j += 4) {
        newData.data[j+0] = Math.min(newData.data[j+0], imgdat.data[j+0]);
        newData.data[j+1] = Math.min(newData.data[j+0], imgdat.data[j+0]);
        newData.data[j+2] = Math.min(newData.data[j+0], imgdat.data[j+0]);
        newData.data[j+3] = 255;
      }
    };
    ctx.putImageData(newData, 0,0);
    setMaxWH({width:max_w, height: max_h});
    setCvsReady(true);
  }

  const draw = () => {
    const c = 1;
    const cvs2 = canvasRef2.current;
    const ctx2 = cvs2.getContext('2d');

    ctx2.clearRect(0,0,maxWH.width,maxWH.height);
    ctx2.beginPath();
    ctx2.lineWidth=lineWidth;
    ctx2.strokeStyle=rectColor;
    ctx2.rect(c*startX, c*startY, c*endX-c*startX, c*endY-c*startY);
    ctx2.stroke();
  }

  const handleMouseDown = e => {
    if (!cvs_ready){return};
    const cvs2 = canvasRef2.current;
    [startX, startY] = getMousePosition(cvs2, e);
    isDown = true;
    getMousePosition(cvs2, e);
  }
  const handleMouseMove = e => {
    if (!cvs_ready){return};
    if (!isDown) return;
    const cvs2 = canvasRef2.current;
    [endX, endY] = getMousePosition(cvs2, e);
   // const dx = mouseX - startX;
   // const dy = mouseY - startY;
   // startX = mouseX;
   // startY = mouseY;
   // dragTarget.x += dx;
   // dragTarget.y += dy;
    draw();
  }

  const handleMouseUp = e => {
    if (!cvs_ready){return};
    if (!isDown){return};
    isDown = false;
    const cvs2 = canvasRef2.current;
    [endX, endY] = getMousePosition(cvs2, e);
    draw();
    const left = Math.round(Math.min(startX, endX));
    const right = Math.round(Math.max(startX, endX));
    const top = Math.round(Math.min(startY, endY));
    const bottom = Math.round(Math.max(startY, endY));
    const rect = {left : left, right: right, bottom: bottom, top: top};
    // left top right bottom
    if (usePercent){
      rect.left = rect.left/maxWH.width*100;
      rect.right = rect.right/maxWH.width*100;
      rect.top = rect.top/maxWH.height*100;
      rect.bottom = rect.bottom/maxWH.height*100;
      if (roundPercent){
        rect.left = Math.round(rect.left);
        rect.right = Math.round(rect.right);
        rect.top = Math.round(rect.top);
        rect.bottom = Math.round(rect.bottom);
      }
    }
    props.recordRect(rect);
  }
  const handleMouseOut = e => {
    handleMouseUp(e);
  }

  const getMousePosition = (canvas, event) => {
    let rect = canvas.getBoundingClientRect(); 
    const cvs2 = canvasRef2.current;
    let x= (event.clientX-rect.left)/(rect.right - rect.left)*cvs2.width
    let y=(event.clientY-rect.top)/(rect.bottom - rect.top)*cvs2.height
    return [x, y]
  }


    return (
      <ClearDiv>
      <canvas ref={canvasRef}
      style={{ position:"absolute", width: "100%",
          display: "block",
          /*margin: "5% auto", left: 0, right:0,*/
          "zIndex": 1
      }}
      /> 
      <canvas width={1000} height={500} ref={canvasRef2}
      style={{position:"absolute", width: "100%",
          display: "inline-block",
          /*margin: "5% auto", left: 0, right:0,*/
          "zIndex": 2
      }}
      onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseOut={handleMouseOut}
      /> 
      {props.img_urls.map((url,i) =>
        <img crossOrigin="anonymous"
        ref={imageRefs[i]}
        src={url}
        key={url}
        onLoad={loadListen}
        style={{visibility: "hidden", position: "fixed"}}
      />)}
</ClearDiv>
    );
}

