import {React, useState} from 'react';
import styled, { css } from 'styled-components';

import {Link} from 'react-router-dom';
import {useDispatch} from 'react-redux';

import { setLib, setURL, setManifestURL, setIIIF } from '../redux/bookSlice.jsx';

const ManifestSelectorDiv = styled.div`
display:flex;
`

const iiif_settings = (lib) => {
  let usePercent = true;
  let roundPercent = true;
  if (lib == 'HRC'){
    usePercent = false;
  }
  return(
    {
      usePercent: usePercent,
      roundPercent: roundPercent
    }
  );
}

export default function ManifestSelector(props){

  const dispatch = useDispatch();

  const [libPick, setLibPick] = useState("");
  const [url1, setUrl1] = useState("");
  const [url2, setUrl2] = useState("");

  const handleChange = (event) => {
    const target = event.target;
    const name = target.name;
    const value = target.value;

    if (name === 'libPick'){setLibPick(value)}
    else if (name === 'url1'){setUrl1(value)}
    else if (name === 'url2'){setUrl2(value)}

  }

  const dispatchManifest = () => {
    dispatch(setURL({url1: url1, url2: url2}));
    dispatch(setLib(libPick));
    dispatch(setIIIF(iiif_settings(libPick)));
  }

  return(
    <ManifestSelectorDiv>
    <form style={{padding: "20px"}}>
    <label>
    Pick your library: 
    <select name="libPick" value={libPick} 
    onChange={handleChange}>
    <option value=""></option>
    <option value="HRC">HRC</option>
    <option value="BL">BL</option>
    <option value="Harvard">Harvard</option>
    <option value="Cambridge">Cambridge</option>
    <option value="IA">Internet Archive</option>
    <option value="Folger">Folger</option>
    <option value="Internal">Internal</option>
    <option value="Experimental">Experimental</option>
    </select>
    </label><br/>

    {libPick !== '' && 
      <div>
      <br/>
      <p>Specify Manifest:</p><br/>
      {libPick === 'HRC' &&
        <p>https://norman.hrc.utexas.edu/notDM/objectManifest/[Piece 1]/[Piece 2]/<br/><br/></p>
      }
      {libPick === 'BL' &&
          <p>https://api.bl.uk/metadata/iiif/ark:/[Piece 1]/[Piece 2]/manifest.json</p>
      }
      {libPick === 'Harvard' &&
          <p>https://iiif.lib.harvard.edu/manifests/[Piece 1]</p>
      }
      {libPick === 'Cambridge' &&
          <p>https://cudl.lib.cam.ac.uk/iiif/[Piece 1]</p>
      }
      {libPick === 'IA' &&
          <p>https://iiif.archive.org/iiif/[Piece 1]/manifest.json</p>
      }
      {libPick === 'Folger' &&
          <p>https://luna.folger.edu/luna/servlet/iiif/m/[Piece 1]/manifest</p>
      }
      {libPick === 'Internal' &&
          <p>Internal use only</p>
      }
      {libPick === 'Experimental' &&
          <p>This uses an experimental manifest parser.  You probably want one of the other choices, unless I have asked you to test this.</p>
      }
      <br/>
      <label>
      URL Piece 1:
      <input name="url1" type="text" 
      value={url1}
      onChange={handleChange} />
      </label>&nbsp;&nbsp;&nbsp;
      {libPick !== 'Harvard' && libPick !== 'Cambridge' && libPick !== 'IA' && libPick !== 'Folger' && libPick !== 'Internal' && libPick !== 'Experimental' &&
          <label>
          URL Piece 2:
          <input name="url2" type="text" 
        value={url2}
        onChange={handleChange} />
          </label>
      }
      <br/><br/>
      <Link to={`/rect/`}><button onClick={dispatchManifest}>Use this manifest</button></Link>

      </div>
    }
    </form>
    </ManifestSelectorDiv>
  );
}

