import {React, useState} from 'react';
import styled, { css } from 'styled-components';

import {useSelector, useDispatch} from 'react-redux';
import {toggleSelected} from '../redux/pagesSlice.jsx';


const PaperImgDiv = styled.div`
  position: relative;
  width: 400px;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  ${({cancelled}) => !cancelled && `
  color='gray';
  background='gray';
  z-index: 10;
  `}

`
const PaperImg = styled.img`
  width: 100%;
  height: auto;
  z-index: 1;
  ${({cancelled}) => cancelled && `
  opacity: 0.1;
  border-color: black;
  border-width: 1px;
  border-style: dotted;
  padding: 0;
  `}
${({cancelled}) => !cancelled && `
  padding: 1px;
  `}
`
const PaperButtonImageR = styled.img`
  position: absolute;
  right: 2px;
  top: 2px;
  z-index: 2;
  ${({active}) => !active && `
  visibility: hidden;
  `}
`
const PaperButtonImageL = styled.img`
  position: absolute;
  top: 2px;
  left: 2px;
  z-index: 2;
${({active}) => !active && `
  visibility: hidden;
  `}
`

export default function PaperScrap(props){

  const dispatch = useDispatch();

  const [hovered, setHovered] = useState(false);
  
  
  //takes event from click.
  const toggleScrap = (e) => {
    dispatch(toggleSelected(props.index));
  }

  const usePercent = useSelector(state => state.book.usePercent);
  const cancelled = useSelector(state => !(state.pages.find(page => page.index == props.index).isSelected));
  const rect = useSelector(state => state.pages.find(page => page.index == props.index).rect);
  
  const url = useSelector(state => state.pages.find(page => page.index == props.index).url);
  const lib = useSelector(state => state.book.lib);

  const rect_str = (usePercent ? 'pct:' : '') + [rect.left, rect.top, rect.right-rect.left, rect.bottom-rect.top].join(',');
  let urlbase = url.split('full')[0];
  urlbase = urlbase + (urlbase[urlbase.length-1]==='/' ? '' : '/');
  const src_url = urlbase + rect_str + "/full/0/default.jpg";


  const RButtonSrc = cancelled ? '/add_icon_small.png' : '/delete_icon_small.png';

    return(
      <PaperImgDiv
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      cancelled={cancelled}
      >
      <PaperImg key={props.index} idx={props.index} ref={props.imageRef}
      crossOrigin="anonymous"
      src={src_url}
      cancelled={cancelled}
      onLoad={props.process}
      />
      <PaperButtonImageL active={hovered} onClick={props.editRect} width="25px" height="25px" src='/edit_icon_small.png' />
      <PaperButtonImageR active={hovered} onClick={toggleScrap} width="25px" height="25px" src={RButtonSrc} />
      </PaperImgDiv>)
}


