const get_manifest_url = (lib, url1, url2) => {
  if (lib === 'HRC'){
    return("https://norman.hrc.utexas.edu/notDM/objectManifest/"+url1+'/'+url2+'/');
  } 
  if (lib === 'BL'){
    return("https://api.bl.uk/metadata/iiif/ark:/"+url1+"/"+url2+"/manifest.json");
  } 
  if (lib === 'Harvard'){
    return("https://iiif.lib.harvard.edu/manifests/"+url1);
  } 
  if (lib === 'Cambridge'){
    return("https://cudl.lib.cam.ac.uk/iiif/"+url1);
  } 
  if (lib === 'IA'){
    return("https://iiif.archive.org/iiif/"+url1+"/manifest.json");
  } 
  if (lib === 'Folger'){
    return("https://luna.folger.edu/luna/servlet/iiif/m/"+url1+"/manifest");
  } 
  if (lib == 'Internal'){
    return(url1)
  }
  if (lib == 'Experimental'){
    return(url1)
  }
  console.log("Oh sad, not the library!");
  //Deal with the unknown library error one day
}

